body,
html {
    .resetmp;
    font-family: @primary-font;

    a,
    button {
        text-decoration: none !important;
        border: none;
        outline: none !important;

        &:focus {
            border: none;
            outline: none;
        }
    }

    main {
        .simplebar-scrollbar {
            width: 10px;

            &::before {
                background: @primary-color-dark;
            }
        }
    }

    .pageLoader {
        position: fixed;
        background-color: #b4e2ea;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        z-index: 888888;
    }

    .cssload-tetrominos {
        position: absolute;
        left: 48%;
        top: 50%;
        z-index: 9999999;
        transform: translate(-50%, -50%);

        // transform: translate(-39px, -34px);
        // -o-transform: translate(-39px, -34px);
        // -ms-transform: translate(-39px, -34px);
        // -webkit-transform: translate(-39px, -34px);
        // -moz-transform: translate(-39px, -34px);
    }

    .cssload-tetromino {
        width: 34px;
        height: 39px;
        position: absolute;
        transition: all ease 0.35s;
        -o-transition: all ease 0.35s;
        -ms-transition: all ease 0.35s;
        -webkit-transition: all ease 0.35s;
        -moz-transition: all ease 0.35s;
        background: url('data:image/svg+xml;utf-8,%3Csvg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 612 684"%3E%3Cpath fill="%2371b1ca" d="M305.7 0L0 170.9v342.3L305.7 684 612 513.2V170.9L305.7 0z"/%3E%3Cpath fill="%23fff" d="M305.7 80.1l-233.6 131 233.6 131 234.2-131-234.2-131"/%3E%3C/svg%3E') no-repeat top center;
    }

    .cssload-box1 {
        animation: cssload-tetromino1 1.73s ease-out infinite;
        -o-animation: cssload-tetromino1 1.73s ease-out infinite;
        -ms-animation: cssload-tetromino1 1.73s ease-out infinite;
        -webkit-animation: cssload-tetromino1 1.73s ease-out infinite;
        -moz-animation: cssload-tetromino1 1.73s ease-out infinite;
    }

    .cssload-box2 {
        animation: cssload-tetromino2 1.73s ease-out infinite;
        -o-animation: cssload-tetromino2 1.73s ease-out infinite;
        -ms-animation: cssload-tetromino2 1.73s ease-out infinite;
        -webkit-animation: cssload-tetromino2 1.73s ease-out infinite;
        -moz-animation: cssload-tetromino2 1.73s ease-out infinite;
    }

    .cssload-box3 {
        animation: cssload-tetromino3 1.73s ease-out infinite;
        -o-animation: cssload-tetromino3 1.73s ease-out infinite;
        -ms-animation: cssload-tetromino3 1.73s ease-out infinite;
        -webkit-animation: cssload-tetromino3 1.73s ease-out infinite;
        -moz-animation: cssload-tetromino3 1.73s ease-out infinite;
        z-index: 2;
    }

    .cssload-box4 {
        animation: cssload-tetromino4 1.73s ease-out infinite;
        -o-animation: cssload-tetromino4 1.73s ease-out infinite;
        -ms-animation: cssload-tetromino4 1.73s ease-out infinite;
        -webkit-animation: cssload-tetromino4 1.73s ease-out infinite;
        -moz-animation: cssload-tetromino4 1.73s ease-out infinite;
    }


    @keyframes cssload-tetromino1 {

        0%,
        40% {
            transform: translate(0, 0);
        }

        50% {
            transform: translate(17px, -9px);
        }

        60%,
        100% {
            transform: translate(34px, 0);
        }
    }

    @-o-keyframes cssload-tetromino1 {

        0%,
        40% {
            -o-transform: translate(0, 0);
        }

        50% {
            -o-transform: translate(17px, -9px);
        }

        60%,
        100% {
            -o-transform: translate(34px, 0);
        }
    }

    @-ms-keyframes cssload-tetromino1 {

        0%,
        40% {
            -ms-transform: translate(0, 0);
        }

        50% {
            -ms-transform: translate(17px, -9px);
        }

        60%,
        100% {
            -ms-transform: translate(34px, 0);
        }
    }

    @-webkit-keyframes cssload-tetromino1 {

        0%,
        40% {
            -webkit-transform: translate(0, 0);
        }

        50% {
            -webkit-transform: translate(17px, -9px);
        }

        60%,
        100% {
            -webkit-transform: translate(34px, 0);
        }
    }

    @-moz-keyframes cssload-tetromino1 {

        0%,
        40% {
            -moz-transform: translate(0, 0);
        }

        50% {
            -moz-transform: translate(17px, -9px);
        }

        60%,
        100% {
            -moz-transform: translate(34px, 0);
        }
    }

    @keyframes cssload-tetromino2 {

        0%,
        20% {
            transform: translate(34px, 0px);
        }

        40%,
        100% {
            transform: translate(50px, 9px);
        }
    }

    @-o-keyframes cssload-tetromino2 {

        0%,
        20% {
            -o-transform: translate(34px, 0px);
        }

        40%,
        100% {
            -o-transform: translate(50px, 9px);
        }
    }

    @-ms-keyframes cssload-tetromino2 {

        0%,
        20% {
            -ms-transform: translate(34px, 0px);
        }

        40%,
        100% {
            -ms-transform: translate(50px, 9px);
        }
    }

    @-webkit-keyframes cssload-tetromino2 {

        0%,
        20% {
            -webkit-transform: translate(34px, 0px);
        }

        40%,
        100% {
            -webkit-transform: translate(50px, 9px);
        }
    }

    @-moz-keyframes cssload-tetromino2 {

        0%,
        20% {
            -moz-transform: translate(34px, 0px);
        }

        40%,
        100% {
            -moz-transform: translate(50px, 9px);
        }
    }

    @keyframes cssload-tetromino3 {
        0% {
            transform: translate(50px, 9px);
        }

        20%,
        60% {
            transform: translate(34px, 19px);
        }

        90%,
        100% {
            transform: translate(17px, 9px);
        }
    }

    @-o-keyframes cssload-tetromino3 {
        0% {
            -o-transform: translate(50px, 9px);
        }

        20%,
        60% {
            -o-transform: translate(34px, 19px);
        }

        90%,
        100% {
            -o-transform: translate(17px, 9px);
        }
    }

    @-ms-keyframes cssload-tetromino3 {
        0% {
            -ms-transform: translate(50px, 9px);
        }

        20%,
        60% {
            -ms-transform: translate(34px, 19px);
        }

        90%,
        100% {
            -ms-transform: translate(17px, 9px);
        }
    }

    @-webkit-keyframes cssload-tetromino3 {
        0% {
            -webkit-transform: translate(50px, 9px);
        }

        20%,
        60% {
            -webkit-transform: translate(34px, 19px);
        }

        90%,
        100% {
            -webkit-transform: translate(17px, 9px);
        }
    }

    @-moz-keyframes cssload-tetromino3 {
        0% {
            -moz-transform: translate(50px, 9px);
        }

        20%,
        60% {
            -moz-transform: translate(34px, 19px);
        }

        90%,
        100% {
            -moz-transform: translate(17px, 9px);
        }
    }

    @keyframes cssload-tetromino4 {

        0%,
        60% {
            transform: translate(17px, 9px);
        }

        90%,
        100% {
            transform: translate(0, 0);
        }
    }

    @-o-keyframes cssload-tetromino4 {

        0%,
        60% {
            -o-transform: translate(17px, 9px);
        }

        90%,
        100% {
            -o-transform: translate(0, 0);
        }
    }

    @-ms-keyframes cssload-tetromino4 {

        0%,
        60% {
            -ms-transform: translate(17px, 9px);
        }

        90%,
        100% {
            -ms-transform: translate(0, 0);
        }
    }

    @-webkit-keyframes cssload-tetromino4 {

        0%,
        60% {
            -webkit-transform: translate(17px, 9px);
        }

        90%,
        100% {
            -webkit-transform: translate(0, 0);
        }
    }

    @-moz-keyframes cssload-tetromino4 {

        0%,
        60% {
            -moz-transform: translate(17px, 9px);
        }

        90%,
        100% {
            -moz-transform: translate(0, 0);
        }
    }
}