section.blog {
    .blog-item {
        display: block;
        color: @dark-font-color;
        margin-bottom: 60px;

        .card {
            .card-body {
                font-family: @primary-font;

                .card-title {
                    margin-top: 15px;
                    font-family: @secondary-font;
                    font-weight: normal;
                    font-size: 18px;
                    line-height: 26px;
                }

                .card-text {
                    text-align: justify;
                    font-size: 15px;
                    line-height: 28px;
                }
            }
        }
    }

    button.loadMoreBlog {
        border-radius: 25px;
        background-color: #44c7f7;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        box-shadow: 0px 3px 5px rgba(black, .2);
        padding: 10px 30px;
        font-weight: 600;
        color: @light-font-color;
        font-size: 20px;
        margin-top: 20px;
        transition: background-color .6s;

        &:hover {
            background-color: darken(#44c7f7, 10%);
        }
    }
}