section.detailsInTab {

    .tab {
        overflow: hidden;
        border-bottom: 2px solid #2c2c2c;
        margin: 0 auto;
        margin-bottom: 30px;

        button {
            background-color: @dark-font-color;
            color: @light-font-color;
            float: left;
            border: none;
            outline: none;
            cursor: pointer;
            padding: 2px 10px;
            transition: 0.3s;
            // font-family: "Calibri";
            font-size: 16px;
            border-left: 1px solid #44c7f7;

            &:hover {
                background-color: #44c7f7;
                color: @dark-font-color;
            }
        }

        button.active {
            background-color: #44c7f7;
            color: @dark-font-color;
        }
    }

    .tabcontent {
        display: none;
        -webkit-animation: fadeEffect 1s;
        animation: fadeEffect 1s;

        article {
            color: @dark-font-color;
            margin-bottom: 40px;

            h3 {
                font-family: "Gotham-Bold";
                font-weight: bold;
                font-size: 20px;
            }

            p {
                font-family: @primary-font-light;
                font-size: 18px;
                line-height: 32px;
            }

            @media (min-width: 768px) {
                h3 {
                    font-size: 23px;
                }

                p {
                    font-size: 18px;
                }
            }
        }

        table {
            font-family: arial, sans-serif;
            border-collapse: collapse;
            width: 100%;
            margin: 0 auto;

            td,
            th {
                text-align: left;
                padding: 5px 20px;
                color: @dark-font-color;
                font-family: @primary-font-light;
                text-transform: capitalize;
                font-size: 10px;

                @media (min-width: 992px) {
                    font-size: 14px;
                }
            }

            @media (min-width: 992px) {
                // width: 95%;
            }

            tr:nth-child(even) {
                background-color: #f0efef;
            }
        }
    }



    /* Fade in tabs */
    @-webkit-keyframes fadeEffect {
        from {
            opacity: 0;
        }

        to {
            opacity: 1;
        }
    }

    @keyframes fadeEffect {
        from {
            opacity: 0;
        }

        to {
            opacity: 1;
        }
    }
}