section.details {
    margin: 100px 0 80px 0;

    .detail-area {
        height: 420px;
        padding: 0px 15px;
        // border: 1px solid @primary-color-dark;
        border-right: none;
        border-left: none;

        article {
            color: @dark-font-color;
            margin-bottom: 40px;

            h3 {
                font-family: "Gotham-Bold";
                text-transform: uppercase;
                font-weight: bold;
                font-size: 20px;
                // text-align: center;
            }

            p {
                font-family: @primary-font-light;
                font-size: 16px;
                line-height: 32px;
                // text-align: center;
            }

            @media (min-width: 768px) {
                h3 {
                    font-size: 23px;
                }

                p {
                    font-size: 16px;
                    text-align: justify;
                }
            }
        }
    }
}