div.quoteForm {
    margin-top: 80px;

    .input-wrap {
        margin-bottom: 40px;

        h5 {
            font-family: "Century Gothic";
            color: @dark-font-color;
            font-weight: bold;
            text-transform: capitalize;
            font-size: 14px;
        }

        input {
            font-family: "Century Gothic";
            color: @dark-font-color;
            font-size: 12px;
            padding: 10px !important;
            width: 100%;
            height: 100%;

            &:focus {
                border-color: @primary-color;
                outline: 0;
                box-shadow: 0 0 0 .2rem rgba(@primary-color, .5);
            }
        }

        input[type=file] {
            padding: 0 !important;
            font-size: 14px;
            margin-top: 10px;
        }

        textarea {
            font-family: "Century Gothic";
            color: @dark-font-color;
            font-size: 12px;
            padding: 10px !important;
            width: 100%;

            &:focus {
                border-color: @primary-color;
                outline: 0;
                box-shadow: 0 0 0 .2rem rgba(@primary-color, .5);
            }
        }

        select.form-control {
            -moz-appearance: none;
            -webkit-appearance: none;
            appearance: none;
            font-family: "Century Gothic";
            color: @dark-font-color;
            font-size: 12px;
            padding: 10px !important;
            height: 100%;

            &:focus {
                border-color: @primary-color;
                outline: 0;
                box-shadow: 0 0 0 .2rem rgba(@primary-color, .5);
            }
        }
    }

    button[type=submit] {
        font-family: @primary-font;
        color: @light-font-color;
        background-color: @dark-font-color;
        text-transform: capitalize;
        padding: 10px 35px;
        border-radius: 25px;
        font-size: 16px;
    }
}