section.testmonials {
    background: #F2F1F1;
    text-align: center;
    padding: 50px 0;

    header {
        text-align: center;

        h1 {
            font-family: "Gotham-Bold";
            font-size: 32px;
            font-weight: bold;
            letter-spacing: -1px;
            text-transform: capitalize;
            margin-bottom: 60px;
            position: relative;

            &::before {
                content: "";
                display: block;
                width: 180px;
                height: 2px;
                background-color: @dark-font-color;
                position: absolute;
                bottom: -15px;
                left: 50%;
                transform: translateX(-50%);
            }

            &::after {
                content: "";
                display: block;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                background-color: @primary-color-dark;
                position: absolute;
                bottom: -24px;
                left: 50%;
                transform: translateX(-50%);
            }
        }

        p {
            font-family: "Century Gothic";
            font-size: 18px;
            font-weight: lighter;
            position: relative;
            text-transform: uppercase;
            display: none;

            &::before {
                content: "";
                display: block;
                width: 180px;
                height: 2px;
                background-color: @dark-font-color;
                position: absolute;
                bottom: -15px;
                left: 50%;
                transform: translateX(-50%);
            }

            &::after {
                content: "";
                display: block;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                background-color: @primary-color-dark;
                position: absolute;
                bottom: -24px;
                left: 50%;
                transform: translateX(-50%);
            }
        }

        @media (min-width: 992px) {
            // padding-top: 50px;

            // h1 {
            //     font-size: 50px;
            // }

            // p {
            //     font-size: 23px;
            // }
        }
    }

    #testimonials-slider {
        margin-bottom: 50px;

        .owl-stage {

            .item {
                opacity: .4;
                transform: scale(.8);
                filter: blur(1px);
                transition: all 1s;
                user-select: none;

                .testimonial {
                    font-family: @primary-font-light;
                    text-align: center;
                    font-size: 12px;
                    letter-spacing: .5px;
                    line-height: 28px;
                }

                .img-wrap {
                    height: 100px;
                    width: 100px;
                    border-radius: 50%;
                    overflow: hidden;
                    transition: all .5s;
                    text-align: center;
                    margin: 30px auto;
                    box-shadow: 0px 0px 3px rgba(black, .2);

                    img {
                        width: 100%;
                    }
                }

                h5 {
                    .resetmp;
                    font-family: @primary-font-semibold;
                    color: @dark-font-color;
                    font-size: 14px;
                    text-align: center;
                }
            }

            .owl-item.active.center {
                .item {
                    opacity: 1;
                    transform: scale(1);
                    filter: blur(0px);
                }
            }
        }
    }


    .dots {
        width: 100px;
        margin: 0 auto;
    }
}