// Seperate Styles in Box By Industry page other then common
section.boxesByIndustry {
    padding-top: 80px;

    .boxes-display {
        padding-top: 40px;

        .beatMyQuote {
            border: 2px solid #2c2c2c;
            text-align: center;
            border-radius: 5px;
            padding: 10px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 auto;
            color: @dark-font-color;
            transition: all .4s;

            h5 {
                .resetmp;
                font-family: "Century Gothic";
                text-align: center;
                transition: all .5s;
                font-size: 17px;
            }

            &:hover {
                background-color: #2c2c2c;

                h5 {
                    color: white;
                }
            }
        }

        .prod-item {
            display: block;
            margin-bottom: 50px;

            .img-wrap {
                background-color: #f4f5f9;
                padding: 20px;
                transition: all .6s;
                border: 1px solid rgb(236, 236, 236);
                position: relative;
                z-index: 22;

                &::before {
                    display: block;
                    content: "";
                    width: 100%;
                    height: 100%;
                    -webkit-box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.3);
                    -moz-box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.3);
                    box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.3);
                    position: absolute;
                    left: 0;
                    top: 0;
                    z-index: 11;
                    opacity: 0;
                }

                img {
                    width: 100%;
                }

                margin-bottom: 15px;
            }

            h4 {
                font-family: "Century Gothic";
                color: @dark-font-color;
                text-transform: capitalize;
                text-align: center;
                font-size: 18px;
            }

            &:hover {
                .img-wrap {
                    transform: scale(1.08);
                    background-color: #f3f8fe;

                    &::before {
                        opacity: .4;
                    }
                }
            }
        }

        .quote-form-wrap {
            padding: 40px 15px;
            // background-image: url("../images/inn-ByBoxes/quote-bg.png");
            background-color: lighten(@primary-color, 5%);
            background-size: cover;
            background-position: center;

            form {

                input {
                    width: 100%;
                    padding: 5px 10px;
                    font-family: "Century Gothic";
                    font-size: 14px;
                    border: none;
                    margin-bottom: 15px;
                    border-radius: 5px;
                }

                .form-group {
                    border: none;

                    select {
                        outline: none !important;
                        border: none;
                        text-transform: uppercase;
                        font-family: "Century Gothic";
                        font-size: 13px;
                        font-weight: bold;
                        height: 100% !important;
                        padding: 5px 10px;
                        border-radius: 7px !important;

                        &:focus {
                            outline: none !important;
                            box-shadow: none;
                        }

                        option {
                            &:hover {
                                background-color: @primary-color  !important;
                                box-shadow: none !important;
                            }
                        }
                    }
                }

                textarea {
                    width: 100%;
                    padding: 5px 10px;
                    font-family: "Century Gothic";
                    font-size: 13px;
                    border: none;
                    // margin: 15px 0;
                    border-radius: 7px;
                    resize: none;
                }

                button {
                    border: 2px solid #2c2c2c;
                    font-family: "Century Gothic";
                    color: @dark-font-color;
                    text-transform: capitalize;
                    border-radius: 25px;
                    padding: 10px 5px;
                    text-align: center;
                    background: transparent;
                    width: 100%;
                    font-size: 16px;
                    margin-top: 50px;
                    transition: all .5s;

                    &:hover {
                        background-color: #2c2c2c;
                        color: #f4f5f9;
                    }
                }
            }
        }
    }
}