.contactUs {
    .contact-item {
        display: block;
        width: 100%;
        color: @dark-font-color;

        p {
            font-family: @primary-font;
            font-weight: bolder;
            font-size: 14px;

            a {
                font-weight: normal;
                color: @dark-font-color;
                margin-left: 5px;
            }
        }
    }

    .icons-wrap {
        display: flex;
        justify-content: flex-end;
        // align-items: center;
        margin-bottom: 15px;

        .social-icon {
            margin-left: 15px;

            img {
                width: 17px;
                transition: all .4s;
                filter: invert(100%) sepia(100%) saturate(100%) hue-rotate(100deg) brightness(101%) contrast(85%);
            }

            &:hover {
                img {
                    filter: none;
                }
            }
        }
    }

    form {
        .input-wrap {

            h5 {
                .resetmp;
                font-family: @primary-font;
                color: @dark-font-color;
                font-weight: bold;
                text-transform: capitalize;
                font-size: 14px;
                margin-bottom: 5px;
            }

            input {
                font-family: "Century Gothic";
                color: @dark-font-color;
                font-size: 12px;
                padding: 10px !important;
                width: 100%;
                height: 100%;
                margin-bottom: 20px;

                &:focus {
                    border-color: @primary-color;
                    outline: 0;
                    box-shadow: 0 0 0 .2rem rgba(@primary-color, .5);
                }
            }

            textarea {
                font-family: @primary-font;
                color: @dark-font-color;
                font-size: 12px;
                padding: 10px !important;
                width: 100%;

                &:focus {
                    border-color: @primary-color;
                    outline: 0;
                    box-shadow: 0 0 0 .2rem rgba(@primary-color, .5);
                }
            }
        }

        button[type=submit] {
            font-family: @primary-font;
            color: @light-font-color;
            background-color: @dark-font-color;
            text-transform: capitalize;
            padding: 10px 15px;
            border-radius: 25px;
            font-size: 15px;
        }
    }
}