section.heroArea {
    background-color: @primary-color;
    // background-image: url("../images/homepage/wave.png");
    // background-repeat: no-repeat;
    // background-position: center bottom -40px;
    // background-size: contain;
    // padding: 60px 0;
    overflow: hidden;
    height: 100vh;
    display: flex;
    align-items: center;
    // margin-top: -60px;

    @media (min-width: 992px) {
        // padding: 60px 0 130px 0;
        margin-top: -50px;
    }


    .hero-text {
        h5 {
            .resetmp;
            font-family: @primary-font-semibold;
            text-transform: uppercase;
            color: @dark-font-color
        }

        h1 {
            .resetmp;
            text-transform: uppercase;
            font-family: @secondary-font-medium;
            font-size: 50px;
            color: lighten(@dark-font-color, 5%);

            @media (min-width: 992px) {
                font-size: 54px;
            }
        }

        p {
            // .resetmp;
            font-size: 15px;
            color: lighten(@dark-font-color, 25%);

            @media (min-width: 992px) {
                // width: 80%;
            }
        }

        a.btn {
            border-radius: 25px;
            background-color: @secondary-color;
            box-shadow: 0px 3px 5px rgba(black, .2);
            padding: 10px 30px;
            font-weight: 600;
            color: @light-font-color;
            font-size: 20px;
            margin-top: 20px;
            transition: background-color .6s;

            &:hover {
                background-color: darken(@secondary-color, 10%);
            }
        }
    }

    .hero-img {
        width: 100%;
        // transform: scale(1.2);
        margin-top: 50px;
        // position: absolute;
        // left: 50%;
        // transform: translateX(-50%);
        // bottom: 0;
        // width: 300px;
        .fadeinUp;

        @media (min-width: 1199px) {
            width: 100%;
            margin-top: -50px;
            // position: absolute;
            // right: 15%;
            // top: 0;
            // width: 650px;
            .fadeinUp;
        }
    }


}

.fadeinUp {
    -webkit-animation: fadeInUp 7s ease-in-out infinite;
    /* Chrome, Safari, Opera */
    animation: fadeInUp 7s ease-in-out infinite;
}

/* Chrome, Safari, Opera */
@-webkit-keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translateY(60px);
    }

    50% {
        opacity: 1;
        transform: translateY(0);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Standard syntax */
@keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translateY(60px);
    }

    25% {
        opacity: 1;
        transform: translateY(0);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}