section.ourServices {
    h2 {
        font-family: "Gotham-Bold";
        font-weight: bold;
        color: @dark-font-color;
        text-transform: capitalize;
        text-align: center;
        margin: 20px 0 50px 0;
    }

    .serviceIcons {
        background-color: @secondary-color;
        // background-image: url("../images/homepage/grid-bg.png");
        // background-repeat: repeat;
        // background-size: contain;
        // background-position: 0 0;
        padding: 40px 0 120px 0;

        .serv-item {
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            transition: all .7s;

            &:hover {}

            img {
                width: 70px;
                filter: brightness(50) invert(1);
            }
        }

        h4 {
            .resetmp;
            text-align: center;
            font-size: 18px;
            text-transform: capitalize;
            width: 75%;
            color: @dark-font-color;
            margin: 0 auto;
            margin-bottom: 50px;
            margin-top: 30px;
            filter: brightness(50) invert(1);


            @media (min-width: 768px) {
                position: absolute;
                top: 100%;
                left: 50%;
                transform: translateX(-50%);
                width: 75%;
                font-size: 18px;
            }
        }
    }
}