section.packaging {
    margin-top: 60px;

    h1 {
        font-family: "Gotham-Bold";
        color: @dark-font-color;
        font-weight: bold;
        text-transform: capitalize;
        text-align: center;
        font-size: 35px;
        line-height: 40px;
        letter-spacing: -1px;
    }

    p {
        font-family: @primary-font-light;
        color: @dark-font-color;
        font-size: 16px;
        line-height: 22px;
        margin-top: 15px;
        text-align: center;
    }

    @media (min-width: 768px) {


        h1 {
            font-size: 48px;
            line-height: 50px;
        }

        p {
            font-size: 20px;
            line-height: 32px;
        }
    }

    @media (min-width: 992px) {

        h1 {
            text-align: left;
        }

        p {
            text-align: left;
        }
    }

    img {
        transition: transform .8s;

        &:hover {
            transform: scale(1.05);
            background-color: #ffffff;
        }
    }
}