div.about-Us-Products {
    padding-bottom: 50px;
    background-color: #faf9f9;
    // background-image: url("../images/homepage/bg-circle.png"), url("../images/homepage/bg-circle.png"), url("../images/homepage/bg-ring2.png");
    // background-repeat: no-repeat;
    // background-size: 0%;
    // background-position: top 9% right -45%, top 35% left -20%, bottom right -15%;

    @media (min-width: 768px) {
        background-size: 40%;
    }

    .ourPros {
        background-color: @secondary-color;
        padding: 50px 0;

        .title {
            text-align: center;
            font-family: @secondary-font-medium;
            margin-bottom: 80px;
            position: relative;
            color: @dark-font-color;

            &::before {
                content: "";
                display: block;
                width: 180px;
                height: 2px;
                background-color: @dark-font-color;
                position: absolute;
                bottom: -25px;
                left: 50%;
                transform: translateX(-50%);
            }

            &::after {
                content: "";
                display: block;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                // background-color: @primary-color-dark;
                position: absolute;
                bottom: -34px;
                left: 50%;
                transform: translateX(-50%);
            }
        }

        .pro-item {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            color: @dark-font-color;

            img {
                width: 40px;
                filter: brightness(100) invert(1);
                transition: all 0.5s;
            }

            &:hover {
                img {
                    position: relative;
                    transform: translateY(-15px);
                }
            }

            h4 {
                .resetmp;
                text-align: center;
                text-transform: capitalize;
                font-size: 14px;
                margin-top: 15px;
                padding: 0 15px;

            }
        }
    }

    .quality-desc {
        padding-top: 120px;

        h1 {
            font-family: "Gotham-Bold";
            font-size: 45px;
            font-weight: bold;
            letter-spacing: -1px;
            position: relative;

            &::before {
                content: "";
                display: block;
                width: 180px;
                height: 2px;
                background-color: @dark-font-color;
                position: absolute;
                top: -25px;
                left: 50%;
                transform: translateX(-50%);
            }

            &::after {
                content: "";
                display: block;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                background-color: @primary-color-dark;
                position: absolute;
                top: -34px;
                left: 50%;
                transform: translateX(-50%);
            }
        }

        p {
            font-family: @primary-font-light;
            font-size: 18px;
            font-weight: lighter;
            position: relative;

            &::before {
                content: "";
                display: block;
                width: 180px;
                height: 2px;
                background-color: @dark-font-color;
                position: absolute;
                bottom: -35px;
                left: 50%;
                transform: translateX(-50%);
            }

            &::after {
                content: "";
                display: block;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                background-color: @primary-color-dark;
                position: absolute;
                bottom: -44px;
                left: 50%;
                transform: translateX(-50%);
            }
        }

        @media (min-width: 992px) {


            h1 {
                font-size: 55px;
            }

            p {
                font-size: 22px;
            }
        }
    }

