    section.categories {
        padding-top: 100px;

        header {
            h1 {
                display: inline-block;
                font-family: @secondary-font;
                text-transform: uppercase;
                letter-spacing: -1px;
                text-align: center;
                position: relative;
                left: 50%;
                transform: translateX(-50%);
                font-size: 24px;

                &:before {
                    content: url('../images/icons/Union.png');
                    display: block;
                    position: absolute;
                    top: 50%;
                    left: -53px;
                    transform: translateY(-50%) scale(.3);
                }

                &:after {
                    content: url('../images/icons/Union.png');
                    display: block;
                    position: absolute;
                    top: 50%;
                    right: -53px;
                    transform: translateY(-50%) scale(.3);
                }

                @media (min-width: 430px) and (max-width: 520px) {

                    &::before {
                        left: -60px
                    }

                    &::after {
                        right: -60px;
                    }
                }

                @media (min-width: 520px) {


                    &::before {
                        left: -65px
                    }

                    &::after {
                        right: -65px;
                    }
                }

                @media (min-width: 768px) {
                    font-size: 38px;
                }
            }
        }

        .products {
            padding-top: 80px;
            // background-image: url("../images/homepage/stars.png");
            // background-repeat: no-repeat;
            // background-size: 90%;
            // background-position: center center;

            .prod-item {
                display: block;
                margin-bottom: 50px;
                padding: 15px;

                .img-wrap {
                    background-color: #f4f5f9;
                    padding: 20px;
                    transition: all .6s;
                    border: 1px solid rgb(236, 236, 236);
                    position: relative;
                    z-index: 22;

                    &::before {
                        display: block;
                        content: "";
                        width: 100%;
                        height: 100%;
                        -webkit-box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.3);
                        -moz-box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.3);
                        box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.3);
                        position: absolute;
                        left: 0;
                        top: 0;
                        z-index: 11;
                        opacity: 0;
                    }

                    img {
                        width: 100%;
                    }

                    margin-bottom: 30px;
                }

                h4 {
                    font-family: @primary-font-light;
                    color: @dark-font-color;
                    text-transform: capitalize;
                    text-align: center;
                    font-size: 18px;
                }

                &:hover {
                    .img-wrap {
                        transform: scale(1.08);
                        background-color: #f3f8fe;

                        &::before {
                            opacity: .4;
                        }
                    }
                }
            }

            a.btn {
                display: inline-block;
                border-radius: 30px;
                background-color: @secondary-color;
                box-shadow: 0px 3px 5px rgba(black, .2);
                font-family: @primary-font;
                padding: 10px 60px;
                color: @light-font-color;
                font-size: 23px;
                margin-top: 20px;
                text-transform: capitalize;
                transition: background-color .6s;

                &:hover {
                    background-color: darken(@secondary-color, 20%);
                }
            }
        }
    }
}