section.blogDetail {
    header {
        margin: 20px 0 15px 0;

        h1 {
            font-family: @secondary-font;
            margin-bottom: 15px;
        }
    }

    .blog-text {
        p {
            font-family: @primary-font;
            font-size: 15px;
            line-height: 27px;
        }

        h3 {
            font-family: @secondary-font;
            font-size: 25px;
            margin-bottom: 15px;
        }

        ul {
            list-style-type: none;
            .resetmp;
            font-family: @secondary-font;
            font-size: 15px;
        }
    }
}