.prods-slider {
    padding: 40px 0;
    overflow-x: hidden;

    .slider-wrap {
        display: flex;
        position: relative;
        width: 100%;

        .left-slider-btn,
        .right-slider-btn {
            width: 40px;
            height: 80px;
            background-color: rgba(@primary-color-dark, .5);
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            z-index: 2222;
            opacity: 0;
            transition: all .4s;

            img {
                width: 15px;
            }

            &:hover {
                background-color: @primary-color-dark;
            }
        }

        &:hover {

            .left-slider-btn,
            .right-slider-btn {
                opacity: 1;
            }
        }

        .left-slider-btn {
            left: 0;
        }

        .right-slider-btn {
            right: 0;

            img {
                transform: rotate(180deg);
            }
        }

        .slide-area {
            width: 100%;

            .item {
                display: block;
                text-align: center;
                overflow: hidden;
                // height: 250px !important;

                img {
                    width: 100%;
                    height: 100%;
                    margin: 0 auto;
                    transition: all .4s;
                    // object-fit: cover;
                    // object-position: center;
                }

                &:hover {
                    img {
                        transform: translateY(-20px);
                    }
                }

            }
        }

    }
}