footer {
    background-color: #2c2c2c;
    // padding-top: 60px;

    .clients-slider {
        text-align: center;
        padding: 60px 0;

        h4 {
            font-family: @primary-font-medium;
            color: @light-font-color;
            margin-bottom: 30px;
            font-size: 20px;
        }

        .item {
            img {
                width: auto;
            }
        }
    }

    .foot-img {
        display: block;
        text-align: center;

        img {
            width: 200px;
        }

        @media (min-width: 992px) {
            text-align: left;
        }
    }

    h5 {
        font-family: "Gotham-Bold";
        color: @primary-color-dark;
        text-transform: capitalize;
        position: relative;
        margin-bottom: 30px;

        &::after {
            content: "";
            display: block;
            width: 70px;
            height: 2px;
            background-color: @secondary-color;
            position: absolute;
            bottom: -10px;
            left: 50%;
            transform: translateX(-50%);
        }

        @media (min-width: 992px) {
            &::after {
                left: 0;
                transform: translateX(0);
            }
        }
    }

    p {
        font-family: @primary-font-light;
        color: @light-font-color;
        font-size: 14px;
        line-height: 28px;
        text-align: center;
        margin-top: 20px;
    }

    .links {
        padding-top: 25px;
        text-align: center;
        text-transform: capitalize;

        a {
            display: block;
            color: @light-font-color;
            font-family: @primary-font-light;
            transition: color .5s;
            font-size: 14px;
            margin-bottom: 10px;

            &:hover {
                color: @primary-color;
            }
        }
    }

    .social-icons {
        display: flex;
        color: @light-font-color;
        align-items: center;
        justify-content: center;
        margin-top: 50px;

        @media (min-width: 992px) {
            justify-content: start;
        }

        a {
            display: block;
            margin-right: 10px;

            img {
                width: 25px;
            }
        }
    }

    @media (min-width:992px) {
        p {
            text-align: left;
        }

        .links {
            text-align: left;
        }
    }

    .footbar {
        padding: 10px 0;
        background-color: #1e1e1e;

        .footwrap {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;

            img {
                width: 250px;

                &:last-of-type {
                    width: 350px;
                }
            }

            p {
                .resetmp;
                font-family: "Century Gothic";
                color: @light-font-color;
                font-size: 14px;
                margin: 10px 0;
            }

            @media (min-width: 992px) {
                flex-direction: row;

                p {
                    margin: 0;
                }
            }
        }
    }
}