.aboutUs {
    a {
        color: @dark-font-color;

        &:hover {
            color: @dark-font-color;
        }
    }

    .detail-area {
        border: 1px solid lightgrey;
        padding: 30px;
        height: 435px;
        font-family: @primary-font-light;
    }

    .social-links {
        .social-wrap {
            margin-bottom: 20px;
            border-radius: 5px;
            overflow: hidden;

            a {
                // display: block;
                width: 100%;
                padding: 30px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                transition: all .5s;
                text-align: center;

                img {
                    width: 30px;
                    transition: all .6s;
                }

                span {
                    font-size: 14px;
                    color: @dark-font-color;
                }

                &:hover {
                    span {
                        color: @light-font-color;
                    }
                }

            }

            &.fb {
                a {
                    box-shadow: inset 0px 0px 10px 1px rgba(#475993, .8);

                    &:hover {
                        background-color: rgba(#475993, .8);

                        img {
                            filter: brightness(0) invert(1);
                            transform: rotate(360deg);
                        }

                        transform: scale(1.3);
                    }
                }
            }

            &.twi {
                a {
                    box-shadow: inset 0px 0px 10px 1px rgba(#65bbf2, .8);

                    &:hover {
                        background-color: rgba(#65bbf2, .8);

                        img {
                            filter: brightness(0) invert(1);
                            transform: rotate(360deg);
                        }

                        transform: scale(1.3);
                    }
                }
            }

            &.insta {
                a {
                    box-shadow: inset 0px 0px 10px 1px rgba(#e72c81, .8);

                    &:hover {
                        background-color: rgba(#e72c81, .8);

                        img {
                            filter: brightness(0) invert(1);
                            transform: rotate(360deg);
                        }

                        transform: scale(1.3);
                    }
                }
            }
        }
    }
}