.triangle-Shapes-wrap {
    position: relative;
    height: 60vh;
    margin-top: 50px;

    .left-tri {
        height: 49vh;
        width: 49vw;
        -webkit-clip-path: polygon(0 0, 0 100%, 100% 50%);
        clip-path: polygon(0 0, 0 100%, 100% 50%);
        background-color: @primary-color;
        background-image: url("../images/Common/tri-img1.png");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        position: absolute;
        top: 0px;
    }

    .right-tri {
        height: 49vh;
        width: 49vw;
        -webkit-clip-path: polygon(0 50%, 100% 100%, 100% 0);
        clip-path: polygon(0 50%, 100% 100%, 100% 0);
        background-color: @primary-color;
        background-image: url("../images/Common/tri-img2.png");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        position: absolute;
        right: 0;
        bottom: 0px;
    }

    @media (min-width: 992px) {
        height: 100vh;
        margin-top: 0;

        .left-tri {
            // height: 750px;
            height: 100vh;
            top: -80px;
            background-attachment: fixed;
            background-repeat: no-repeat;
            background-size: 50%;
            background-position: left 40px;
            position: absolute;
            top: -10%;
            // top: 0;
        }

        .right-tri {
            // height: 750px;
            height: 100vh;
            bottom: -80px;
            background-attachment: fixed;
            background-size: 50%;
            background-position: top right;
            position: absolute;
            right: 0;
            bottom: -15%;
            // bottom: 0;
        }
    }

    // @media (min-width: 1200px) {
    //     height: 50vh;

    //     .left-tri {
    //         height: 800px;
    //         background-size: 50%;
    //         background-position: left 40px;
    //     }

    //     .right-tri {
    //         height: 800px;
    //         background-size: 50%;
    //         background-position: top right;
    //     }
    // }
}