header.commonHeader {
    text-align: center;

    h1 {
        .resetmp;
        color: @dark-font-color;
        font-family: "Gotham-Bold";
        font-weight: bolder;
        text-transform: uppercase;
        letter-spacing: -1px;
        font-size: 50px;
    }

    p {
        .resetmp;
        color: @dark-font-color;
        font-family: "Century Gothic";
        text-transform: uppercase;
        font-size: 14px;
        position: relative;

        &::before {
            content: "";
            display: block;
            width: 200px;
            height: 2px;
            border-radius: 2px;
            background-color: @dark-font-color;
            position: absolute;
            bottom: -15px;
            left: 50%;
            transform: translateX(-50%);
        }

        &::after {
            content: "";
            display: block;
            width: 60px;
            height: 6px;
            border-radius: 2px;
            background-color: @primary-color-dark;
            position: absolute;
            bottom: -17px;
            left: 50%;
            transform: translateX(-50%);
        }
    }
}