.related-products {
    margin-top: 100px;
    margin-bottom: 80px;

    @media (min-width: 768px) {
        margin-bottom: 0;
    }

    .prod-item {
        display: block;
        margin-bottom: 50px;

        .img-wrap {
            background-color: #f4f5f9;
            padding: 10px;
            transition: all .6s;
            border: 1px solid rgb(236, 236, 236);
            position: relative;
            z-index: 22;

            &::before {
                display: block;
                content: "";
                width: 100%;
                height: 100%;
                -webkit-box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.3);
                -moz-box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.3);
                box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.3);
                position: absolute;
                left: 0;
                top: 0;
                z-index: 11;
                opacity: 0;
            }

            img {
                width: 100%;
            }

            margin-bottom: 15px;
        }

        h4 {
            font-family: "Century Gothic";
            color: @dark-font-color;
            text-transform: capitalize;
            text-align: center;
            font-size: 14px;
        }

        &:hover {
            .img-wrap {
                transform: scale(1.08);
                background-color: #ffffff;

                &::before {
                    opacity: .4;
                }
            }
        }
    }

    a.btn {
        display: inline-block;
        border-radius: 30px;
        background-color: #44c7f7;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        box-shadow: 0px 3px 5px rgba(black, .2);
        padding: 10px 30px;
        font-weight: 600;
        color: @light-font-color;
        font-size: 22px;
        margin-top: 20px;
        text-transform: capitalize;
        transition: background-color .6s;

        &:hover {
            background-color: darken(#44c7f7, 15%);
        }
    }
}