section.works {
    header {
        padding-top: 100px;
        text-align: center;

        h1 {
            font-family: "Gotham-Bold";
            font-size: 45px;
            font-weight: bold;
            letter-spacing: -1px;
            text-transform: capitalize;
            position: relative;

            &::before {
                content: "";
                display: block;
                width: 180px;
                height: 2px;
                background-color: @dark-font-color;
                position: absolute;
                top: -25px;
                left: 50%;
                transform: translateX(-50%);
            }

            &::after {
                content: "";
                display: block;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                background-color: @primary-color-dark;
                position: absolute;
                top: -34px;
                left: 50%;
                transform: translateX(-50%);
            }
        }

        p {
            font-family: @primary-font-light;
            font-size: 18px;
            font-weight: lighter;
            position: relative;
            text-transform: uppercase;

            &::before {
                content: "";
                display: block;
                width: 180px;
                height: 2px;
                background-color: @dark-font-color;
                position: absolute;
                bottom: -15px;
                left: 50%;
                transform: translateX(-50%);
            }

            &::after {
                content: "";
                display: block;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                background-color: @primary-color-dark;
                position: absolute;
                bottom: -24px;
                left: 50%;
                transform: translateX(-50%);
            }
        }

        @media (min-width: 992px) {
            // padding-top: 50px;

            h1 {
                font-size: 50px;
            }

            p {
                font-size: 23px;
            }
        }
    }

    .steps {
        text-align: center;
        margin-top: 60px;

        h1 {
            font-family: "Gotham-Bold";
            font-size: 80px;
            color: @dark-font-color;
            position: relative;
            z-index: 999;

            &::before {
                content: url("../images/icons/Union.png");
                // display: block;
                position: absolute;
                top: 50%;
                left: 50%;
                z-index: -1;
                transform: translate(-50%, -40%) scale(.6);
            }
        }

        p {
            font-family: @primary-font;
            color: @dark-font-color;
            font-size: 26px;
            line-height: 25px;
            text-transform: capitalize;
        }

        @media (min-width:768px) and (max-width: 992px) {
            p {
                font-size: 20px;
            }
        }

        a.btn {
            display: inline-block;
            border-radius: 30px;
            background-color: @secondary-color;
            box-shadow: 0px 3px 5px rgba(black, .2);
            font-family: @primary-font;
            padding: 12px 25px;
            color: @light-font-color;
            font-size: 23px;
            margin-top: 25px;
            text-transform: capitalize;
            transition: background-color .6s;

            &:hover {
                background-color: darken(@secondary-color, 20%);
            }
        }
    }
}