header.mainHeader {
    background-color: @primary-color;

    .search-bar {
        background-color: @dark-font-color;
        padding: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: fixed;
        width: 100%;
        top: -100px;
        left: 0;
        z-index: 999999;
        transition: top .5s;

        img {
            width: 20px;
            height: 20px;
            filter: invert(99%) sepia(100%) saturate(36%) hue-rotate(17deg) brightness(105%) contrast(100%);
        }

        input {
            width: 100%;
            display: inline-block;
            border-radius: 10px;
            background-color: transparent;
            border-color: grey;
            margin: 0 20px;
            padding: 5px 10px;
            color: @light-font-color;
            font-family: "Century Gothic";
        }

        .close-searchBar {
            cursor: pointer;
            width: 50px;
            color: @light-font-color;
        }
    }

    .headtop {
        background-color: @primary-color;
        padding: 14px;
        text-align: center;
        -webkit-box-shadow: 0 2px 4px -2px rgba(8, 8, 8, .4);
        -moz-box-shadow: 0 2px 4px -2px rgba(8, 8, 8, .4);
        box-shadow: 0 2px 4px -2px rgba(8, 8, 8, .4);

        // -webkit-box-shadow: 0px 5px 5px -2px rgba(0, 0, 0, 0.75);
        // -moz-box-shadow: 0px 5px 5px -2px rgba(0, 0, 0, 0.75);
        // box-shadow: 0px 5px 5px -2px rgba(0, 0, 0, 0.75);
        // position: relative;
        // z-index: 999999;
        .top-links {
            display: flex;
            justify-content: end;

            a {
                color: @dark-font-color;
                margin-left: 20px;
                font-size: 12px;
                font-family: @primary-font-light;

                i {
                    font-family: @primary-font-medium;
                    font-style: normal;
                }
            }
        }

        .social-icons-wrap {
            display: none;
            flex-direction: column;
            align-items: center;
            width: 50px;
            position: absolute;
            top: 125px;
            right: 18px;
            z-index: 111;

            .v-line {
                height: 50px;
                width: 2px;
                background-color: @dark-font-color;
            }

            .social-icon {
                display: inline-block;

                img {
                    width: 19px;
                    transition: transform .4s;
                }

                &:hover {
                    img {
                        transform: scale(1.2);
                    }
                }
            }

            @media (min-width: 992px) {
                display: flex;
            }
        }
    }

    nav.navbar {
        background-color: transparent;
        // padding: 25px 10px;
        transition: padding .4s;
        // padding: 0;
        border: none;

        .navbar-brand {
            .logo-img {
                width: 180px;
                transition: all .6s;
            }
        }

        .navbar-nav {
            .nav-item {
                text-transform: capitalize !important;
                font-family: @primary-font-medium;

                .nav-link {
                    display: block;
                    height: 100%;
                    padding: 30px 18px;
                    text-transform: uppercase !important;
                    font-size: 15px;
                    transition: all .6s;
                }

                .nav-btn {
                    span {
                        color: @light-font-color  !important;
                        text-transform: capitalize !important;
                        background-color: @dark-font-color;
                        font-weight: bold;
                        padding: 5px 15px;
                        font-size: 12px;
                        border-radius: 25px;
                    }
                }

                .search {
                    cursor: pointer;

                    img {
                        width: 20px;
                    }
                }

                @media (min-width: 992px) and (max-width:1199px) {
                    .nav-link {
                        padding: 10px;
                        font-size: 13px;
                    }
                }
            }

            #megaMenu.nav-item.dropdown {
                position: static !important;

                .dropdown-menu {
                    width: 100% !important;

                    .dropdown-item {
                        width: 20%;
                    }
                }

                .dropdown-menu.show {
                    display: flex !important;
                    flex-direction: column !important;

                    @media (min-width: 992px) {
                        flex-direction: row !important;
                        flex-wrap: wrap !important;
                    }
                }
            }
        }
    }

    .dropdown-toggle::after {
        transition: transform 0.15s linear;
    }

    .show.dropdown .dropdown-toggle::after {
        transform: translateY(3px);
        transition: all .5s !important;
    }

    .dropdown-menu {
        .resetmp;
        margin-top: 0;
        background-color: lighten(@secondary-color, 25%);
        transition: all .5s !important;
        width: auto;
        overflow: hidden;

        @media (min-width: 992px) {
            width: 300px;
        }

        .dropdown-item {
            display: flex;
            align-items: center;
            transition: all .5s;
            border-left: 0px solid transparent;
            padding: 10px;
            font-size: 13px;
            // width: 20%;

            img {
                width: 33px;
                margin-right: 10px;
            }

            &:hover {
                border-width: 3px;
                border-color: @dark-font-color;
                padding-left: 15px;
            }
        }
    }

    nav.sticky {
        transition: all .5s;
        box-shadow: 2px 2px 2px rgba(black, 0);
    }

    nav.sticky.is-sticky {
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        z-index: 88888;
        width: 100%;
        background-color: @primary-color;
        box-shadow: 2px 2px 2px rgba(black, .2);
    }
}